import React, { useState } from 'react';
import './menu.css';
// import { Link } from 'gatsby';

const menuItems = [ 'Home', 'Rooms', 'Photos', 'About' ];

// class MenuItems extends React.Component
const MenuItems = () => {
	const [ toggle, setToggle ] = useState(false);
	const [ classOn, setClassOn ] = useState('');
	const [ hidden, setHidden ] = useState('hidden');
	// const MenuToggle = document.querySelector('.menu-toggle');
	// const menuSection = document.querySelector('.menu-section');
	// const MenuSection = document.createElement('div');
	// const MenuSection = () => {
	// 	return <div />;
	// };
	// const MenuSection = React.createElement('div', null);
	// const MenuToggle = document.createElement('div');
	// const MenuToggle = () => {
	// 	return <div />;
	// };
	// const MenuToggle = React.createElement('div', null);
	// const NavUl = document.createElement('ul');
	// const NavUl = () => {
	// 	return <ul />;
	// };
	// const NavUl = document.querySelector('nav').querySelector('ul');
	// const NavUl = React.createElement('ul', null);
	// console.log(MenuSection, MenuToggle, NavUl);
	function toggleOnMenu(event) {
		setToggle(!toggle);
		if (toggle) {
			setClassOn('on');
			setHidden('');
		} else {
			setClassOn('');
			setHidden('hidden');
		}
		navHide(event);
		// event.stopPropagation();
		// console.log(this);
	}

	// function navHide(event) {
	// 	NavUl.classList.toggle('hidden');
	// 	console.log(event.target);
	// 	event.preventDefault();
	// 	// event.stopPropagation();
	// }

	return (
		// menu-section
		<div onClick={toggleOnMenu} className={classOn + ' menu-section'} style={{}}>
			{/* menu-toggle */}
			<div className={classOn + ' menu-toggle'} style={{}}>
				{/* one */}
				<div className="one" style={{}} />
				{/* two */}
				<div className="two" style={{}} />
				{/* three */}
				<div className="three" style={{}} />
			</div>
			<nav>
				<ul
					role="navigation"
					className={hidden}
					style={{
						// display: 'none'
					}}
				>
					{menuItems.map((menuItem) => {
						return (
							<li key={menuItem}>
								<a href="/">{menuItem}</a>
							</li>
						);
					})}
				</ul>
			</nav>
		</div>
	);
};

